import axios from "axios";
import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import styled from "styled-components";
import { mobile } from "../responsive";
import { productAction } from "../slice";
import React from "react";
const Container = styled.div`
  flex: 1;

  margin: 3px;
  margin-top: 30px;
  height: 70vh;
  position: relative;
`;

const Image = styled.img`
  width: 100%;
  height: 100%;
  object-fit: cover;
  ${mobile({ height: "40vh" })}
`;

const Info = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: end;
`;

const Title = styled.h4`
  color: ${(props) =>
    props.color == "OUR FAVOURITES" ? "lightblack" : "white"};
  margin-bottom: 0px;
`;

const Button = styled.button`
  border: none;
  padding: 10px;
  background-color: black;
  color: lightpink;
  cursor: pointer;
  font-weight: 600;
`;

const FavouriteItem = ({ item, title }) => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const setProduct = productAction.setProduct;
  const fetchProduct = async (name) => {
    await axios
      .post("/api/getSomeProduct", {
        productName: name,
      })
      .then((res) => {
        console.log(res.data[0]);
        const item = res.data[0];
        dispatch(setProduct({ item }));
        navigate("/Product");
      })
      .catch((err) => {
        console.log(err);
      });
  };
  return (
    <Container>
      <Image src={item.img} />
      <Info>
        <Button
          onClick={() => {
            console.log(item.title);
            fetchProduct(item.title);
          }}
        >
          SHOP NOW
        </Button>
        <Title color={title}>{item.title}</Title>
      </Info>
    </Container>
  );
};

export default FavouriteItem;
