import React from "react";
import { useNavigate } from "react-router-dom";
import styled from "styled-components";
import { mobile } from "../responsive";

const Container = styled.div`
  flex: 1;

  margin: 3px;
  margin-top: 30px;
  height: 70vh;
  position: relative;
`;

const Image = styled.img`
  width: 100%;
  height: 100%;
  object-fit: cover;
  ${mobile({ height: "40vh" })}
`;

const Info = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: end;
`;

const Title = styled.h4`
  color: ${(props) =>
    props.color == "OUR FAVOURITES" ? "lightblack" : "white"};
  margin-bottom: 0px;
`;

const Button = styled.button`
  border: none;
  padding: 10px;
  background-color: black;
  color: lightpink;
  cursor: pointer;
  font-weight: 600;
`;

const CategoryItem = ({ item, title }) => {
  const navigate = useNavigate();
  return (
    <Container>
      <Image src={item.img} />
      <Info>
        <Button
          onClick={() => {
            navigate(`/ProductList/${item.title.toLowerCase()}`);
          }}
        >
          SHOP NOW
        </Button>
        <Title color={title}>{item.title}</Title>
      </Info>
    </Container>
  );
};

export default CategoryItem;
